.container1 {
  position: relative;
  background-color: #000000;
  padding: 100px 28px 25px 15px;
  width: 268px;
  border-radius: 12px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.441);
  margin-top: -270px;
  margin-left: 500px;
}

.container2 {
  position: relative;
  background-color: #ffffff;
  padding: 20px 6px 10px 15px;
  width: 290px;
  border-radius: 12px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.272);
