.input {
  display: block;
  width: 245px;
  height: 17px;
  padding: 0.375rem 0.50rem;
  font-family: Decima-mono;
  font-size: 22px;
  line-height: 1.5;
  color: #3f566d;
  background-color: #010101;
  background-clip: padding-box;
  border: 1px solid #000000;
  font-weight: 900;
  text-align: right;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  outline: none;
}


.output {
  display: block;
  width: 245px;
  height: 25px;
  padding: 0.375rem 0.50rem;
  font-family: Decima-mono;
  font-size: 40px;
  line-height: 1.5;
  color: #ffffff;
  background-color: #010101;
  background-clip: padding-box;
  border: 1px solid #000000;
  font-weight: 800;
  text-align: right;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  outline: none;
}

